.rightbar
{
    flex:4
}


.rightbarWrapper
{
    padding:20px 20px 0px 0px ;
}

.birthdayContainer
{
    display: flex;
    align-items: center;

}

.birthdayGift
{
    height: 40px;
    width: 40px;
    margin-right: 5px;

}

.birthdayText
{
    font-size: 15px;
    font-weight: 300;

}

.rightbarAdd
{
  width: 100%;
  border-radius: 10px;
  margin-top: 10px;

}

.onlineSection
{
    margin-bottom: 20px;
}


.onlineFriendList
{
    margin: 0;
    padding: 0;
    list-style: none;
}

.onlineFriend
{
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 15px;
}


.profileImgContainer
{
    margin-right: 10px;
    position: relative;
}



.profileImg
{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}


.rightbarOnline
{
    width: 12px;
    height: 12px;
    background-color: green; 
    border-radius: 50%;
    border: 2px solid white;
    top: -2px;
    position: absolute;
    right:0;
}

.userName
{

    font-weight:500;
}